@import '~assets/styles/h3Imports';

.marketplace-card {
  background-color: var(--recomendation-card-color-background);
  border-radius: 16px;
  display: flex;
  overflow: hidden;
}

.image-container {
  background-color: #cccccc;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  width: 50%;
}

.card-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: em(44) em(56);
  width: 50%;
}

.time-read {
  color: var(--gray-5);
  font-size: em(14);
  font-weight: 600;
  margin-bottom: em(8, 14);
}

.card-tags {
  border: 1px solid var(--gray-3);
  border-radius: 4px;
  box-sizing: border-box;
  color: var(--gray-3);
  font-size: em(12);
  font-weight: bold;
  margin-bottom: em(16, 12);
  padding: em(4, 12) em(12, 12);
  width: fit-content;
}

.card-description {
  color: var(--gray-5);
  font-size: em(33);
  font-weight: bold;
  margin: 0;
}

.card-price {
  font-size: em(19);
  font-weight: bold;
  margin: 0;
  margin-top: em(16, 19);
}

.button-wrap {
  margin-top: 32px;
}

.button-wrap button {
  margin-bottom: unset;
  min-width: fit-content;
  padding: em(10) em(32);
}

@media #{$medium-down} {
  .card-description {
    font-size: em(27);
  }

  .card-content {
    padding: em(52) em(32);
  }
}

@media #{$small-down} {
  .marketplace-card {
    flex-direction: column-reverse;
  }

  .card-content {
    padding: em(32) em(16);
    width: 100%;
  }

  .image-container {
    height: em(232);
    width: 100%;
  }

  .card-description {
    font-size: em(27);
  }

  .button-wrap button {
    min-width: 100%;
  }
}
